/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;
@keyframes spin-heads {
  0% {
    transform: rotateX(0); }
  100% {
    transform: rotateX(1980deg); } }

@keyframes spin-tails {
  0% {
    transform: rotateX(0); }
  100% {
    transform: rotateX(2160deg); } }

@keyframes move-coin {
  0% {
    transform: translateY(0%); }
  20% {
    transform: translateY(-30%); }
  80% {
    transform: translateY(-1%); }
  100% {
    transform: translateY(0%); } }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  box-sizing: border-box; }

.wrapper {
  position: relative;
  height: 100vh;
  background-image: linear-gradient(to right, #ffb901 40%, #00833f 100%); }

.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 0px 1rem 2rem rgba(0, 0, 0, 0.8);
  height: 90%;
  width: 90%;
  max-width: 777px;
  border: 3px solid #ffce51; }
  .card-header {
    font-size: 3rem;
    border-bottom: 1px solid #ccc;
    width: 100%;
    text-align: center;
    padding: 1.7rem; }
  .card-body {
    width: 100%; }
  .card-footer {
    border-top: 1px solid #ccc;
    padding: 1.7rem;
    width: 100%; }
    .card-footer .guess {
      height: 100%; }
      .card-footer .guess-title {
        font-size: 2rem;
        margin-top: 1rem 0;
        text-align: center; }
      .card-footer .guess-score {
        font-size: 4rem;
        font-weight: 600;
        margin: 1rem 0;
        text-align: center; }
      .card-footer .guess-options {
        border: 2px solid #ca9100;
        border-radius: 50px;
        font-size: 1.3rem;
        overflow: hidden; }
        .card-footer .guess-options .selected {
          background-color: #ffb901;
          color: #fff;
          font-weight: 700; }
        .card-footer .guess-options .not-selected {
          transition: all 0.3s ease-in-out; }
          .card-footer .guess-options .not-selected:hover {
            background-color: rgba(255, 206, 81, 0.42); }
        .card-footer .guess-options-option {
          letter-spacing: 3px;
          text-transform: uppercase;
          font-weight: 500;
          width: 100%;
          height: 100%;
          padding: 1rem;
          text-align: center;
          cursor: pointer; }

.coin-wrapper {
  margin-bottom: 0px;
  animation: move-coin 3s infinite; }
  .coin-wrapper .coin-container {
    position: relative;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    height: 150px;
    width: 150px;
    animation: spin-tails 3s infinite;
    transform: rotateX(0) translateY(0); }
    .coin-wrapper .coin-container .coin {
      cursor: pointer;
      position: absolute;
      border: 10px solid #ca9100;
      background-image: linear-gradient(to right bottom, #ffb901, #ffce51, #ffb901);
      color: #fff;
      font-size: 2rem;
      height: 150px;
      width: 150px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      backface-visibility: hidden;
      -webkit-backface-visibility: hidden; }
      .coin-wrapper .coin-container .coin-tails {
        transform: rotateX(180deg); }
